.FlexiImg {
  .row {
    display: grid;
    grid-template-columns: auto auto;

    @media only screen and (min-width: 700px) {
      display: flex;
    }

    .col {
      height: 188px;
      width: 100%;
      background-size: cover;
      background-position: center;

      @media only screen and (min-width: 700px) {
        height: 447px;
        width: 25%;
      }
    }
  }
}
